@import './variables.css';
@import './base.css';
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Roboto:wght@400;500&display=swap');

@media (min-width: 768px) {
  .content {
    padding: 0 var(--spacing-5);
  }
}

.app-container {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background: var(--primary-color);
  font-family: 'Roboto', sans-serif;
  color: var(--font-color);
}

.content-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.content {
  flex: 3;
  overflow: hidden;
  height: 100%;
  animation: fadeIn 1s ease both; /* Smooth fade-in effect */
}

/* Navigation Bar */
.nav {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-links {
  display: flex;
  gap: var(--spacing-3);
}

.nav-links a {
  position: relative;
  color: var(--secondary-color);
  text-decoration: none;
  font-weight: 400;
  padding-bottom: 4px;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.nav-links a::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--secondary-color);
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s ease;
}

.nav-links a:hover::after,
.nav-links a.active::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.nav-links a:hover,
.nav-links a.active {
  color: var(--secondary-color);
  font-weight: 500;
}

.nav-links a.active {
  color: var(--secondary-color);
  font-weight: 600;
}
